* {
  padding: 0;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}


.main_div {
  width: 100%;
  background-color: #6983aa;
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.center_div{
  width: 25%;
  height: 80vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgb(0, 0, 0, 0.5);
  border-radius: 15px;
}

h1{
  color: white;
  background: transparent;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px a5px -5px rgb(0, 0, 0, 0.3);
}
input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
   background: transparent;
   font-size: 20px;
   font-weight: 500;
   width: 60%;
   border-bottom: 2px solid #8566aa;
   outline: none;

}

button{
  min-height: 40px;
  width: 40px;
  border-radius: 50%;
  border-color: transparent;
  background-color: #8586aa;
  color: #fff;
  font-size: 30px;
  border: none;
  outline: none;
  margin-left: 5px;
  box-shadow: 5px 5px a5px -5px rgb(0, 0, 0, 0.3);

}

button:hover{
  background-color: #20bf6b;
}
ol{
  margin-top: 30px;
}
ol li{
  padding-left: 0;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  display: flex;
  align-items: center;
  color: #8586aa;
  text-transform: uppercase;
}
.todo_style{
  display: flex;
  flex-direction: row;
  /* justify-content: center; */
  align-items: center;
}

.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #8586aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px a5px -5px rgb(0, 0, 0, 0.3);
}
.todo_style .fa-times:hover{
background-color: crimson;
}

.todo_style .fa-times:hover .list_style{
  color: crimson;
  }
  @media (max-width:768px) {
    .center_div {
      width: 75%;
    }
  }

  .clock_color div{
    color: black !important;
  }